.bannerVideoCoverage {
    position: fixed;
    bottom: 20px;
    left: 20px;
    width: 400px;
    height: 225px;
    margin: 0px;
    padding: 0px;
    border: none;
    -webkit-box-shadow: -1px 2px 71px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: -1px 2px 71px 0px rgba(0, 0, 0, 0.75);
    box-shadow: -1px 2px 71px 0px rgba(0, 0, 0, 0.75);

    .bottomVideo {
        background-color: black;
        width: 400px;
        height: 225px;
        background-image: url("../../assets/gif/tv.gif");
    }

    #closeVideo:hover {
        animation: shake;
        animation-duration: 2s;
    }

    video[poster] {
        border: inherit;
        object-fit: cover;
        width: 400px;
        height: calc(300px * 1.71428);
    }

    .vjs-poster {
        background-size: cover;
        background-position: inherit;
    }
}